/* PrivacyPolicy.css */
.privacy-policy {
  padding: 40px;
  font-family: "Inter", sans-serif;
  background-color: #f9f9f9;
  line-height: 1.6; /* –æ—Å–Ω–æ–≤–Ω–∏–π –º—ñ–∂—Ä—è–¥–∫–æ–≤–∏–π —ñ–Ω—Ç–µ—Ä–≤–∞–ª –¥–ª—è –≤—Å—å–æ–≥–æ –±–ª–æ–∫—É */
}

.privacy-policy h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  padding-left: 0.5rem;
}

.privacy-policy .popular-title {
  margin-bottom: 40px;
}

.privacy-policy .dot {
  width: 18px;
  height: 18px;
  background: #D31F37;
  display: inline-block;
  margin-right: 8px;
}

.policy-section {
  margin-bottom: 2rem;
}

.policy-section h3 {
  font-size: 24px;
  margin-bottom: 20px;
  color: black;
}

.policy-section p,
.policy-section ul,
.policy-section li {
  font-size: 18px;
  color: black;
  line-height: 1.6; /* —Ä—ñ–≤–Ω–∏–π –º—ñ–∂—Ä—è–¥–∫–æ–≤–∏–π —ñ–Ω—Ç–µ—Ä–≤–∞–ª –¥–ª—è –≤—Å—å–æ–≥–æ —Ç–µ–∫—Å—Ç—É */
}

.policy-section ul {
  list-style: disc;
  margin-left: 1.5rem;
}

/* –ó–º—ñ–Ω–∞ –∫–æ–ª—å–æ—Ä—É –º–∞—Ä–∫–µ—Ä—ñ–≤ —Å–ø–∏—Å–∫—É */
.policy-section ul li::marker {
  color: #D31F37;
}

.policy-section a {
  color: #c00;
  text-decoration: none;
}

.policy-section a:hover {
  text-decoration: underline;
}
